#view-wrapper h1,
#view-wrapper h2,
#view-wrapper h3,
#view-wrapper h4,
#view-wrapper h5,
#view-wrapper h6 {
  margin: 40px 0 0;
}
#view-wrapper ul, #view-wrapper ol, #view-wrapper dl {
  margin-top: 0;
  margin-bottom: 1rem;
}
#view-wrapper dt {
  font-weight: bold;
}
#view-wrapper dd {
  margin-bottom: .5rem;
}
#view-wrapper ul li {
  list-style-type: square;
}
#view-wrapper table {
  margin-bottom: 1rem;
  width: 100%;
  border: 1px solid #e5e5e5;
  border-collapse: collapse;
}
#view-wrapper td,
#view-wrapper th {
  padding: .25rem .5rem;
  border: 1px solid #e5e5e5;
}
#view-wrapper tbody tr:nth-child(odd) td,
#view-wrapper tbody tr:nth-child(odd) th {
  background-color: #f9f9f9;
}
#view-wrapper figure {
  padding: 20px 0;
  margin: 0;
}

#view-wrapper figure figcaption {
  text-align: center;
  font-size: 0.8rem;
  color: #898D96;
  word-break: keep-all;
}
#view-wrapper a {
  text-decoration: none;
  color: #268bd2;
}
#view-wrapper hr {
  position: relative;
  margin: 1.5rem 0;
  border: 0;
  border-top: 1px solid #eee;
  border-bottom: 1px solid #fff;
}
#view-wrapper abbr {
  font-size: 85%;
  font-weight: bold;
  color: #555;
  text-transform: uppercase;
}
#view-wrapper abbr[title] {
  cursor: help;
  border-bottom: 1px dotted #e5e5e5;
}
#view-wrapper img {
  display: block;
  max-width: 100%;
  margin: 20px auto;
  border-radius: 5px;
}


#view-wrapper code,
#view-wrapper pre {
  font-family: Menlo, Monaco, "Courier New", monospace;
}
#view-wrapper code {
  padding: .25em .5em;
  font-size: 85%;
  color: #bf616a;
  background-color: #f9f9f9;
  border-radius: 3px;
}
#view-wrapper pre {
  display: block;
  margin-top: 0;
  margin-bottom: 1rem;
  padding: 1rem;
  font-size: .9rem;
  line-height: 1.4;
  white-space: pre;
  white-space: pre-wrap;
  word-break: break-all;
  word-wrap: break-word;
  background-color: #f9f9f9;
  border-radius: 5px;
}
#view-wrapper pre code {
  padding: 0;
  font-size: 100%;
  color: inherit;
  background-color: transparent;
}
#view-wrapper blockquote {
  padding: .5rem 1rem;
  margin: .8rem 0;
  color: #7a7a7a;
  border-left: .25rem solid #e5e5e5;
}
#view-wrapper blockquote p {
  margin: 0;
}
@media (min-width: 30em) {
  #view-wrapper blockquote {
    padding-right: 5rem;
    padding-left: 1.25rem;
  }
}


